#welcome-div {
  text-align: center;
}

#welcome-div p {
  font-size: 30px;
  margin: 0;
  margin-top: 8px;
}

table {
  margin: 30px auto 0 auto;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border-collapse: collapse;
  width: 800px;
}

td,
th {
  border: 1px solid rgb(212, 210, 210);
  text-align: left;
  padding: 8px;
}

th {
  background-color: blueviolet;
  color: white;
}

#redirect-btn-div {
  margin-top: 25px;
  display: flex;
  justify-content: center;
}

#redirect-btn-div button {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 600;
  font-size: 16px;
  background-color: blueviolet;
  color: white;
  padding: 10px 30px 10px 30px;
  border-radius: 4px;
}

#redirect-btn-div button:hover {
  background-color: rgb(183, 120, 242);
}
