.homepage header {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 10px;
}

.homepage h1 {
  font-size: 26px;
}

.homepage nav {
  background-color: #555;
}

.homepage nav ul {
  list-style-type: none;
  margin: 0;
  padding: 10px;
  display: flex;
  justify-content: left;
}

.homepage nav li {
  margin: 10 10px;
}

.homepage nav a {
  text-decoration: none;
  color: #fff;
}

.homepage main {
  padding: 20px;
}

.homepage section {
  background-color: #fff;
  padding: 20px;
  margin-bottom: 200px;
  border-radius: 5px;
}
