.calculator {
  width: 300px;
  margin: 0 auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}
input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  font-size: 20px;
}
.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5px;
}
.button {
  width: 100%;
  padding: 10px;
  font-size: 20px;
  text-align: center;
  background-color: rgba(8, 71, 54, 0.741);
  color: white;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #45a049;
}
